//Class for userinformation object
export class OwnInfo {
  constructor(name, address, zip, city, country, bId, phone, email, iban, bic) {
    this.name = name;
    this.address = address;
    this.zip = zip;
    this.city = city;
    this.country = country;
    this.bId = bId;
    this.phone = phone;
    this.email = email;
    this.iban = iban;
    this.bic = bic;
  }
}

export default OwnInfo;



